:root {
	--bg: #e3e4e8;
	--fg: #17181c;
	--search--input: #ffffff;
	--primary: #255ff4;
	--dur: 1s;
	/* font-size: calc(16px + (24 - 16)*(100vw - 320px)/(1280 - 320)); */
}
.search--body, .search--input {
	color: #17181c;
	color: var(--fg);
	font: 1em/1.5 ,sans-serif;
}
.search--body {
	background: #e3e4e8;
	background: var(--bg);
	display: flex;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
.search--form, .search--input, .caret {
	margin: auto;
}
.search--form {
	position: relative;
	width: 100%;
	max-width: 17em;
}
.search--input, .caret {
	display: block;
	transition: all calc(1s * 0.5) linear;
	transition: all calc(var(--dur) * 0.5) linear;
}
.search--input {
    background: transparent;
    border-radius: 50%;
    box-shadow: 0 0 0 0.25em inset;
    caret-color: #255ff4;
    caret-color: var(--primary);
    width: 1.75rem;
    height: 1.75rem;
    -webkit-appearance: none;
            appearance: none;
    cursor: pointer;
}
.search--input:focus, .search--input:valid {
	background: #ffffff;
	background: var(--search--input);
	border-radius: 0.25em;
	box-shadow: none;
	padding: 0.75em 0.75em;
	transition-duration: calc(1s * 0.25);
	transition-duration: calc(var(--dur) * 0.25);
	transition-delay: calc(1s * 0.25);
	transition-delay: calc(var(--dur) * 0.25);
	width: 100%;
	height: 2em;
}
.search--input:focus {
	-webkit-animation: showCaret 1s steps(1);
	        animation: showCaret 1s steps(1);
	-webkit-animation: showCaret var(--dur) steps(1);
	        animation: showCaret var(--dur) steps(1);
	outline: transparent;
}
.search--input:focus + .caret, .search--input:valid + .caret {
	-webkit-animation: handleToCaret 1s linear;
	        animation: handleToCaret 1s linear;
	-webkit-animation: handleToCaret var(--dur) linear;
	        animation: handleToCaret var(--dur) linear;
	background: transparent;
	width: 1px;
	height: 1.5em;
	-webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
	        transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
}
.search--input::-webkit-search-decoration {
	-webkit-appearance: none;
}
.search--label {
	color: #e3e4e8;
	overflow: hidden;
	position: absolute;
	width: 0;
	height: 0;
}
.caret {
	background: currentColor;
	border-radius: 0 0 0.125em 0.125em;
	margin-bottom: -0.6em;
	width: 0.25em;
	height: 1em;
	-webkit-transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
	        transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
	-webkit-transform-origin: 50% 0;
	        transform-origin: 50% 0;
}

/* Dark mode */
@media (prefers-color-scheme: dark) {	
	:root {
		--bg: #17181c;
		--fg: #e3e4e8;
		--search--input: #2e3138;
		--primary: #5583f6;
	}
}

/* Animations */
@-webkit-keyframes showCaret {
	from {
		caret-color: transparent;
	}
	to {
		caret-color: #255ff4;
		caret-color: var(--primary);
	}
}
@keyframes showCaret {
	from {
		caret-color: transparent;
	}
	to {
		caret-color: #255ff4;
		caret-color: var(--primary);
	}
}
@-webkit-keyframes handleToCaret {
	from {
		background: currentColor;
		width: 0.25em;
		height: 1em;
		-webkit-transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
		        transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
	}
	25% {
		background: currentColor;
		width: 0.25em;
		height: 1em;
		-webkit-transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
		        transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
	}
	50%, 62.5% {
		background: #255ff4;
		background: var(--primary);
		width: 1px;
		height: 1.5em;
		-webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,2.5em);
		        transform: translate(0,-1em) rotate(-180deg) translate(7.5em,2.5em);
	}
	75%, 99% {
		background: #255ff4;
		background: var(--primary);
		width: 1px;
		height: 1.5em;
		-webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
		        transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
	}
	87.5% {
		background: #255ff4;
		background: var(--primary);
		width: 1px;
		height: 1.5em;
		-webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,0.125em);
		        transform: translate(0,-1em) rotate(-180deg) translate(7.5em,0.125em);
	}
	to {
