.modalContainer{
  --light: hsl(0, 0%, 100%);
    --background: linear-gradient(to right bottom, hsl(140deg 6% 57%), hsl(184deg 31% 42%));
    /* --background: linear-gradient(to right bottom, hsl(236, 50%, 50%), hsl(195, 50%, 50%)); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Open Sans', sans-serif;
    min-height: 100vh;
    background: linear-gradient(to bottom, hsl(236, 50%, 98%), hsl(236, 50%, 94%));
  }
  .modal-container {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    z-index: 10;
      justify-content: center;
    align-items: center;
  
    width: 100%;
    height: 100%;
  
    background: hsla(0, 0%, 40%, .6);
  }
  .modal__btn.secondary--nav{
    color: #2196f3;
     background-color: #efefef;
  }
  /* using :target */
