    display: flex;
  }
  
  .modal {
    width: 50%;
    padding: 1rem;
     /* 4rem 2rem; */
    border-radius: .8rem;
    color: var(--light);
    background: var(--background);
    box-shadow: .4rem .4rem 2.4rem .2rem hsla(236, 50%, 50%, 0.3);
    position: relative;
    overflow: hidden;
  }
  
  .modal__title {
    font-size: 2rem;
  }
  .form__container{
    display: grid;
    justify-content: center;
  }
  .modal__description {
    margin-top: 1rem;
    font-size: 1.6rem;
    font-style: italic;
  }
  .modal__details{
    width: 100%;
    display: grid;
    margin-right: 4rem;
  }
  .modal__text {
    margin-bottom: 1rem;
  }
  .fileLabel{
    cursor: pointer;
    font-size: 1vw;
  }
  .uploadFile{
    display: none;
  }
  .flex-row-file{
    align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 1em;
  }
  .lf-label-file{
    font-size: 3rem;
    display: contents;
    align-items: center;
    justify-content: center;
    background: #f5f6f8;
    cursor: pointer;
  }
  .modal__text::before {
    content: '';
    position: absolute;
    top: 0%;
    left: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 18rem;
    height: 18rem;
    border: 1px solid hsla(0, 0%, 100%, .2);
    border-radius: 100rem;
    pointer-events: none;
}
  
  .modal__btn {
    margin: auto;
    justify-content: center;
    justify-self: center;
    display: flex;
    padding: 1rem 1rem;
    border: 1px solid hsla(0, 0%, 100%, .4);
    border-radius: 100rem;
    color: inherit;
    background: transparent;
    font-size: 1.4rem;
    font-family: inherit;  
    transition: .2s;
    cursor: pointer;
    max-width: -webkit-fill-available;
  }
  
  .modal__btn:hover,
  .modal__btn:focus {
    border-color: hsla(0, 0%, 100%, .6);
    -webkit-transform: translateY(-.2rem);
            transform: translateY(-.2rem);
  }
  
  /* links */
  /* =============================================== */
  .link-1 {
    font-size: 1.8rem;
  
    color: var(--light);
    background: var(--background);
    box-shadow: .4rem .4rem 2.4rem .2rem hsla(236, 50%, 50%, 0.3);
    border-radius: 100rem;
    padding: 1.4rem 3.2rem;
  
    transition: .2s;
  }
  
  .link-1:hover,
  .link-1:focus {
    -webkit-transform: translateY(-.2rem);
            transform: translateY(-.2rem);
    box-shadow: 0 0 4.4rem .2rem hsla(236, 50%, 50%, 0.4);
  }
  
  .link-2 {
    width: 4rem;
    height: 4rem;
    border: 1px solid hsla(0, 0%, 100%, .4);
    border-radius: 100rem;
    background: none;
    color: inherit;
    font-size: 2.2rem;
  
    position: absolute;
    top: 2rem;
    right: 2rem;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    transition: .2s;
  }
  
  .link-2::before {
    content: '\D7';
  
    -webkit-transform: translateY(-.1rem);
  
            transform: translateY(-.1rem);
  }
  
  .link-2:hover,
  .link-2:focus {
    border-color: hsla(0, 0%, 100%, .6);
    -webkit-transform: translateY(-.2rem);
            transform: translateY(-.2rem);
  }
  
  /* Second Version Link */
  /* =============================================== */
  .second-version-link,
  .second-version-link:link {
    color: hsl(236, 50%, 50%);
    padding: .8rem 1.6rem .8rem .2rem;
    border-bottom: 2px solid hsl(236, 50%, 50%);
  
    font-size: 1.4rem;
    font-weight: bold;
  
    position: absolute;
    top: 4rem;
    right: 4rem;
  }
  
  .second-version-link::after {
    content: '\2197';
  
    position: absolute;
    top: 0;
    right: 0;
  
    font-size: .9em;
  }
  
  .abs-site-link {
